.preloader{color:#fff;-webkit-animation:rotater 1s infinite;animation:rotater 1s infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;padding:0;margin:0;font-size:4.375rem;transform-origin:center;line-height:1;width:4.375rem;height:4.375rem}.preloader-wrapper{margin:8rem 0;display:grid;place-items:center;width:100%}@-webkit-keyframes rotater{from{transform:rotate(0)}to{transform:rotate(360deg)}}@keyframes rotater{from{transform:rotate(0)}to{transform:rotate(360deg)}}

.chart{height:11.5rem;margin-bottom:.625rem;font-size:1rem !important}.chart canvas{margin-right:0 !important}.chart-title{font-size:1.5625rem;text-align:center;margin:0 0 .625rem 0;font-weight:300}.chart-wrapper{width:49%}.chart-wrapper:nth-child(1){width:100%}@media (max-width: 1024px){.chart-wrapper{height:21.96vw}}@media (max-width: 1024px){.chart{height:17.96vw}}.charts-box{width:100%;display:flex;justify-content:space-between;flex-wrap:wrap}

.room{border-top:1px solid #5c5c5c}.room__error{color:#df5555;font-weight:400;margin-bottom:1.25rem}.room__game-info{display:grid;place-items:center;grid-row-gap:1.25rem;row-gap:1.25rem;grid-template-rows:.875rem 1.25rem 3.125rem 1.25rem;margin:1.25rem 0;border-radius:1.25rem}.room__map{font-size:1.875rem;color:#919191}.room__region{display:flex;align-items:center;font-size:1rem;color:#919191}.room__region img{margin-left:.625rem;width:2.5rem}.room__endtime{font-size:1.25rem;color:#919191}.room__btn{margin-top:1.25rem;color:#26282b}.room__score{font-size:3.125rem;font-weight:700}.room__score-half{margin-left:1.875rem}.room__score-box{display:flex;justify-content:space-between;align-items:center;font-size:.875rem}.room__team-header{display:flex;justify-content:space-between}.room__team-name{margin:1.25rem 0;font-size:1.5625rem;font-weight:700}.room__team-wrapper{display:flex;flex-direction:column;border-radius:1.25rem}.room__team-wrapper:nth-of-type(2){margin-bottom:1.25rem}.room__team-1,.room__team-2{display:flex;flex-direction:column}.room__team-winner .player-wrapper{background:#415d61}.room__team-loser .player-wrapper{background:#683a49}.player-wrapper{display:grid;place-items:center;grid-template-columns:3.75rem 4fr 1fr 2fr repeat(3, 1fr) repeat(3, 2fr) repeat(4, 1fr);font-size:1.125rem;height:3.125rem;padding:0 .3125rem;border-radius:1.5625rem;margin-bottom:.625rem}.player-captions{background:none;height:1.25rem;font-size:.875rem;color:#919191}.player-captions__header{text-align:center}.player__avatar{width:2.5rem;height:2.5rem;border-radius:50%}.player__avatar:not([src]){content:''}.player__info div{margin-bottom:.3125rem}.player__js-start{justify-self:start}.player__js-start:hover{cursor:pointer}

